.filters-wrapper {
	padding: 0 10px 0 20px !important;
}

.active-cat {
	color: #475696 !important;

	span {
		color: #475696 !important;
	}
}

.range-counter {
	width: 100%;
	margin-top: 25px;
	display: flex;
	justify-content: space-between;

	span {
		color: #475696;
		font-size: 18px;
	}
}

.size-option .active::after {
	content: "";
	position: absolute;
	left: 0;
	top: 0;
	height: 100%;
	width: 100%;
	border-radius: 50%;
	background: white;
	border: 2px solid #f26c4f;
}

.color--filter h4 {
	margin-top: 40px !important;
}

.widget-color-form .color-option .color-1 span {
	background-color: #fff !important;
	border: none;
}

.widget-color-form .color-option .color-2 span {
	background-color: #111 !important;
	border: none;
}

.widget-color-form .color-option .color-3 span {
	background-color: #371e17 !important;
	border: none;
}

.widget-color-form .color-option .color-4 span {
	background-color: #bd111d !important;
	border: none;
}

.widget-color-form .color-option .color-5 span {
	background-color: #213152 !important;
	border: none;
}

.widget-color-form .color-option .color-6 span {
	background-color: #777 !important;
	border: none;
}

.widget-color-form .color-option .color-7 span {
	background-color: #cb9692 !important;
	border: none;
}

.widget-color-form .color-option .color-8 span {
	background-color: #4c5634 !important;
	border: none;
}

.widget-color-form .color-option .color-9 span {
	background-color: #888fa2 !important;
	border: none;
}

.widget-color-form .color-option .color-10 span {
	background-color: #b8b195 !important;
	border: none;
}

.widget-color-form .color-option span {
	display: flex;
	justify-content: center;
	align-items: center;

	svg {
		display: none ;
	}
}

.widget-color-form .color-option span.active {
	svg {
		display: block;
		width: 70%;
		color: #fff;
	}
}

.widget-color-form .color-option .color-1 span.active {
	svg {
		width: 90%;
		color: #111 !important;
	}
}

@media (max-width: 991px) {

	.filters-wrapper {
		padding: 0 30px 50px !important;
	}
	
}